@import "../../sassUtils/mixins.scss";
@import "../../sassUtils/vars.scss";

.Services{
  // @include border-test(red);
  width: 90%;
  height: auto;
  padding: 10% 5% 40% 5%;
  display: grid;
  grid-template-columns: 20% 80%;
  grid-template-rows: auto;

  & .Services__title{
    // border: 1px solid black;
    height: auto;
    grid-column: 1/2;
    padding: 50% 0;
    padding-left: 15%;
    position: relative;
    transform: rotate(270deg);
    & div{
      // border: 1px solid green;
      font-size: 4rem;
      line-height: 80px;
      
    }

    & .Services__decorate{
      width: 100px;
      height: 2px;
      background: #255429;
      // position: absolute;
      // right: 0;
    }
  }

  & .Services__icons{
    // border: 1px solid black;
    grid-column: 2/3;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    & .Services__icons-1{
      // border: 1px solid black;
      grid-column: 1/2;
      @include flexbox(center,flex-start);
      padding: 3%;
      & img{
        // border: 1px solid black;
        max-width: 95%;
      }
    }
    & .Services__icons-2{
      // border: 1px solid black;
      grid-column: 2/3;
      @include flexbox(center,flex-end);
      padding: 5%;
      & img{
        // border: 1px solid black;
        max-width: 95%;
      }
    }
    & .Services__icons-3{
      // border: 1px solid black;
      grid-column: 3/4;
      @include flexbox(center,flex-start);
      padding: 5%;
      & img{
        // border: 1px solid black;
        max-width: 95%;
      }
    }
  }
}


@media screen and(max-width: 830px){

 .Services{
  // border: 1px solid black;
  grid-template-columns: 1fr;
  grid-template-rows: 0.2fr 1fr;
  padding: 0;
  width: 100%;
  margin-bottom: 100px;
 }

 .Services .Services__title{
  grid-column:1/2 ;
  grid-row: 1/2;
  transform: none;
  padding: 0;
  padding-left: 5%;
 }

 .Services .Services__icons{
  // border: 1px solid red;
  grid-column: 1/2;
  grid-row: 2/3;
  @include flexbox(center,center,column);
 }

 .Services .Services__icons{
  position: relative;
 }
 .Services__icons-decorate{
  position: absolute;
  width: 50%;
  height: 100%;
  background-color: #255429;
  right: 0;
  z-index: -1;
  border-bottom-left-radius: 15px;
  border-top-left-radius: 15px;
  opacity: 0.5;
 }
}

